@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(#243278, #111d54);
  position: relative;
}
.date__compo {
    /* border: 2px solid red; */
    position: absolute;
    top: 8%;
    left: 15%;
    color: #babdca;
}

.date__parta {
    display: flex;
    padding: 10px 10px;
}

.date__parta h3 {
    margin-left: 12px;
}

.date__partb h4 {
    margin-left: 12px;
    padding-left: 10px;
}
.totalAmount {
    background-image:
     radial-gradient(ellipse farthest-corner at 80vw 15vh ,
     #222f76 5%, #222f76 95%
  );
    padding: 20px;
    width: 500px;
    height: 200px;
    border-radius: 8px;
    box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    color: #fff;
    margin-bottom: 20px;

}

.totalAmount__title {
    margin-bottom: 20px;
    /* border: 2px solid violet; */
    padding: 5px;
    color: #d2d4e0;
    font-size: 20px;
}

.totalAmount_data {
    text-shadow: -2px 0px 19px #2f4287;
    text-align: center;
    color: #fff;
    font-size: 40px;
    font-family: 'Roboto', sans-serif;
}

.dashboard {
    /* border: 2px solid red; */
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 50px;
    padding-left: 0px;
}
.packet {
    /* border: 2px solid yellowgreen; */
    padding: 10px 20px;
    margin-top: 20px;
}

.packet__h3 {
    color: #99a1c5;
}

.packet__h4 {   
    color: #5593aa;
}
.recent {
    background-image:
     radial-gradient(ellipse farthest-corner at 80vw 15vh ,
     #222f76 5%, #222f76 95%
    /* border: 2px solid red; */
  );
    padding: 20px;
    width: 500px;
    height: 200px;
    border-radius: 8px;
    box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    color: #fff;
}

.recent__data {
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mainBody {
    /* border: 2px solid red; */
    position: absolute;
    top: 20%;
    left: 18%;
    padding: 20px;
}

.mainBody h1 {
    color: #cfd2df;
    font-size: 35px;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1px;
    margin-bottom: 40px;
}

.newEntry {
    /* border: 2px solid red; */
    background-image:
     radial-gradient(ellipse farthest-corner at 80vw 15vh ,
     #222f76 5%, #222f76 95%
  );
    padding: 50px;
    width: 900px;
    height: 400px;
    border-radius: 8px;
    box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    color: #fff;

}

form {
    display: flex;
    flex-direction: column;
}

.newEntry input {
    padding: 10px 20px;
    font-size: 18px;
    width: 50%;
    margin: auto;
    margin-top: 19px;
    margin-bottom:20px;
}

.newEntry button {
    padding: 10px 5px;
    width: 30%;
    margin: auto;
    margin-top: 30px;
}
.people {
    /* border: 2px solid red; */
    background-image:
     radial-gradient(ellipse farthest-corner at 80vw 15vh ,
     #222f76 5%, #222f76 95%
  );
    padding: 50px;
    width: 900px;
    /* height: 400px; */
    border-radius: 8px;
    box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -webkit-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 4px 31px 0px rgba(0,0,0,0.75);
    color: #fff;

}

form {
    display: flex;
    flex-direction: column;
}

.people input {
    padding: 10px 20px;
    font-size: 18px;
    width: 50%;
    margin: auto;
    margin-top: 19px;
    margin-bottom:20px;
}

.people button {
    padding: 10px 5px;
    width: 30%;
    margin: auto;
    margin-top: 30px;
}

.peoples {
    /* border: 2px solid plum; */
    padding: 10px;
    margin-top: 20px;
    overflow: hidden;
    overflow-x: hidden;
}
.member {
    /* border: 2px solid rebeccapurple;  */
    text-align: center;
    margin-bottom: 20px;
    background-color: #202d71;
    padding: 10px;
    display: inline-block;
    margin-right: 12px;
}

.member a {
    color: #fff;
    text-decoration: none;
    font-size: 20px;
}




.indi a {
    color: #fff;
    text-decoration: none;
    font-size: 20px;
    background-color: #212e73;
}

.de {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #212e73;
    margin: 20px;
}

.de h2 {
    margin-bottom: 20px;
}



.de button {
    background-color: #212e73;
    color: #fff;
    border: 0;
    font-size: 20px;
    width: 120px;
    cursor: pointer;
    
}

.de.specialC {
    background-color: red;
    
}

button.specialC {
    background-color: red;
}

.de h3.specialC {
    -webkit-transform: translateX(130px);
            transform: translateX(130px);
}

.expense__dates {
    /* border: 2px solid purple; */
    margin: 20px;
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    padding: 15px;
    border-radius: 5px;
    /* background-color: #252525; */
    color: #fff;
    font-family: 'Baloo Chettan 2', cursive;

}

.expense__month {
    font-size: 20px;
    margin-right: 10px;
}

.expense__date {
    font-size: 28px;
    
}

.expense__year {
    margin-right: 10px;
}
.navbar {
    
    height: 100vh;
    width: 13%;
    
    background-color: #212e73;
    font-family: 'Open Sans', sans-serif;
}


.navbar__img {
    color: #fff;
    font-size: 100px;
    text-align: center;
    margin-bottom: 80px;
    padding-top: 25px;
}



.navbar__option {
    color: #838aaf;
    font-size: 13px;
    padding: 12px;
    text-align: center;
    width: 60%;
    margin: auto;
    margin-bottom: 12px;
    cursor: pointer;
    text-align: center;
}

.navbar__option:hover {
    font-size: 17px;
}

.navbar__option h4 {
    margin-top: 12px;
}

.navbar__option.special {
    background-color: #1f2d6f;
    color: #fff;
}
.navdetails {
    /* border: 2px solid firebrick; */
    position: absolute;
    top: 0;
    right: 0;
    width: 20%;
    background-color: #26347c;
    display: flex;
    padding: 20px;
    margin: auto;
    padding: auto;
}

.navdetails div {
    flex-grow: 1;
    font-size: 20px;
    color: #f7f8fc;
    cursor: pointer;
}

.navdetails div:hover {
    font-size: 25px;
}
